.recipe {
  border-radius: 10px;
  box-shadow: 0px 5px 20px rgb(71, 71, 71);
  margin: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background: rgb(207, 181, 181);
  align-items: center;
  min-width: 40%;
}
.image {
  width: 200px;
  height: 200px;
  border-radius: 70%;
}
